  methods: {
    vueOnScroll() {
      var prev = window.pageYOffset;
      const header = this.$refs.headRef;
      const logo = this.$refs.logo;
      const lang = this.$refs.lang;
      const arrow = this.$refs.arrow;
      const arrow2 = this.$refs.arrow2;
      const arrow3 = this.$refs.arrow3;
      const button = this.$refs.button;
      const burger = this.$refs.burger;
      window.addEventListener("scroll", () => {
        var curr = window.pageYOffset;
        if (prev > curr) {
          header.classList.add("scrollUp");
          header.classList.remove("scrollDown");
          logo.classList.add("logo-gray");
          logo.classList.remove("logo-white");
          lang.classList.add("logo-gray");
          lang.classList.remove("logo-white");
          arrow.style.fill = "#404145";
          arrow2.style.fill = "#404145";
          arrow3.style.fill = "#404145";
          button.classList.add("sign-up__scroll");
          button.classList.remove("sign-up__btn");
          burger.style.backgroundColor = "#2B2B2B";
          this.$refs["about"].$el.style.color = "#2B2B2B";
          this.$refs["trainers"].$el.style.color = "#2B2B2B";
          this.$refs["pro"].$el.style.color = "#2B2B2B";
          this.$refs["sub"].$el.style.color = "#2B2B2B";
          this.$refs["blog"].$el.style.color = "#2B2B2B";
          this.$refs["contact"].$el.style.color = "#2B2B2B";
        } else {
          header.classList.add("scrollDown");
          header.classList.remove("scrollUp");
        }
        if (curr === 0) {
          header.classList.remove("scrollDown");
          header.classList.remove("scrollUp");
          logo.classList.add("logo-white");
          logo.classList.remove("logo-gray");
          lang.classList.add("logo-white");
          lang.classList.remove("logo-gray");
          arrow.style.fill = "#FFFFFF";
          arrow2.style.fill = "#FFFFFF";
          arrow3.style.fill = "#FFFFFF";
          button.classList.add("sign-up__btn");
          button.classList.remove("sign-up__scroll");
          burger.style.backgroundColor = "rgba(255, 255, 255, 0.5)";
          this.$refs["about"].$el.style.color = "#FFFFFF";
          this.$refs["trainers"].$el.style.color = "#FFFFFF";
          this.$refs["pro"].$el.style.color = "#FFFFFF";
